<!-- 雷达图 -->
<template>
  <div id="radar"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {};
  },
  // props: {
  //   radarConfig: Object
  // },
  mounted() {
    this.$x.get(this.url.climateFunanment, {}).then(res => {
      if (res.status == "10000") {
        let nowArr = [
          res.data.current.demand,
          res.data.current.economic,
          res.data.current.fund,
          res.data.current.stock,
          res.data.current.oilsd
        ];
        
        let lastArr = [
          res.data.weekago.demand,
          res.data.weekago.economic,
          res.data.weekago.fund,
          res.data.weekago.stock,
          res.data.weekago.oilsd
        ];
        nowArr.forEach((ele,index)=>{
          nowArr[index] = parseInt(ele).toFixed(0)
        })
        
        // let nowArr = [60, 73, 85, 40, 60]; //今天
        //需求 经济 资金 库存 供应
        // let lastArr = [23, 90, 23, 32, 67];//昨天
        let mychart = echarts.init(document.getElementById("radar"));
        var option = {
          name:{
            nameGap :55
          },
          color:['#F56476','#00A4CF'],
          legend: {
            // 图例组件
            show: true,
            icon: "circle",
            bottom: "8%", // 图例距离顶部边距
            left: "18%", // 图例距离左侧边距
            itemWidth: 28, // 图例标记的图形宽度。[ default: 25 ]
            itemHeight: 28, // 图例标记的图形高度。[ default: 14 ]
            itemGap: 73, // 图例每项之间的间隔。[ default: 10 ]横向布局时为水平间隔，纵向布局时为纵向间隔。
            orient: "horizontal", // 图例列表的布局朝向,'horizontal'为横向,''为纵向.
            textStyle: {
              // 图例的公用文本样式。
              fontSize: 30,
              color: "#fff",
              padding: [0, 0, 0, 10]
            }
          },
          radar: [
            {
              indicator: [
                { text: "需求", max: 100 },
                { text: "经济", max: 100 },
                { text: "资金", max: 100 },
                { text: "库存", max: 100 },
                { text: "供应", max: 100 }
              ],
              //   center: ["50%", "54%"], //调整雷达图的位置
              radius: 142, //半径，可放大放小雷达图
              axisLine: {
                //坐标轴线相关设置
                show: true,
                lineStyle: {
                  color: "#474C6C"
                }
              },
              splitLine: {
                show: true,
                lineStyle: {
                  width: 2,
                  color: "#474C6C" // 图表背景网格线的颜色
                }
              },
              splitArea: {
                show: false
              }
            }
          ],
          series: [
            {
              type: "radar",
              symbol: "none", //去掉拐点的圈
              data: [
                {
                  value: nowArr,
                  name: "当前",
                  areaStyle: {
                    normal: {
                      color: "rgba(244, 105, 116, 0.4)"
                    }
                  },
                  lineStyle: {
                    color: "#F56476"
                  }
                },
                {
                  value: lastArr,
                  name: "一周前",
                  areaStyle: {
                    normal: {
                      color: "rgba(22, 119, 208, 0.6)" // 图表中各个图区域的颜色
                    }
                  },
                  lineStyle: {
                    color: "#00A4CF" // 图表中各个图区域的边框线颜色
                  }
                }
              ]
            }
          ]
        };
        mychart.setOption(option);
        var i = -1;
        mychart.setOption({
          radar: [
            {
              name: {
                rich: {
                  a: {
                    color: "#fff",
                    fontSize: "38",
                    fontWeight:400,
                    align: "center",
                    lineHeight: 36,
                  },
                  b: {
                    color: "rgba(245, 100, 118, 1)",
                    align: "center",
                    fontSize: "40",
                    padding: 2,
                    borderRadius: 4,
                    lineHeight: 50
                  }
                },
                formatter: (a, b) => {
                  i++;
                  return `{a|${a}}\n\n{b|${nowArr[i]}}`;
                }
              }
            }
          ]
        });
      }
    });
  }
};
</script>
<style  scoped>
#radar {
  width: 550px;
  height: 640px;
}
</style>